@import url('https://fonts.googleapis.com/css?family=Comfortaa:700|Cormorant+Garamond:700|Open+Sans&subset=cyrillic');

/* font-family: 'Comfortaa', cursive;
font-family: 'Cormorant Garamond', serif;
font-family: 'Open Sans', sans-serif; */

body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li,
ol li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

body {
  background-color: #9ee0ca;
  font-size: 20px;
  font-family: 'Comfortaa', cursive;
  color: #1f2d2e;
}

a {
  color: #1f2d2e;
  text-decoration: none;
}

a:hover,
a:active,
a:focus {
  transition: all 0.15s ease-in-out;
}

a:focus {
  outline-color: #1f2d2e;
}

button:focus {
  outline-color: #1f2d2e;
}

button:hover,
button:active {
  outline: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  display: inline-block;
  padding: 3px 20px;
  font-family: 'Open Sans', sans-serif;
  font-size: 25px;
  font-weight: 900;
  text-transform: uppercase;
  color: #9ee0ca;
  background-color: #1f2d2e;
  border-radius: 10px;
  border: 3px solid #252525;
}

.button:hover {
  background-color: #15d289;
  border-color: #fff;
}

.button:active {
  background-color: #10a46b;
}

.main-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 5px 0;
  background-color: #446655;
  color: #1f2d2e;
  z-index: 2;
}

.main-header--container {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: center;
}

.main-header--nav {
  width: calc(50% - 30px);
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  margin-top: auto;
  margin-bottom: auto;
}

.main-header--list {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.main-header--item {
  display: inline-block;
}

.main-header--item__active .main-header--link {
  color: #1f2d2e;
}

.main-header--link {
  display: block;
  color: #1f2d2e;
}

.main-header--link:hover,
.main-header--link:focus {
  color: #d2f9f9;
}

.main-header--link:active {
  color: #d2f9f9;
}

.main-header--mobile {
  padding: 0;
  border: none;
  font-size: 0;
  background-color: transparent;
  display: none;
  margin: -5px 0;
}

.main-header--mobile::before {
  content: "â‰¡";
  color: #d2f9f9;
  font-size: 40px;
}

.main-header--purchase {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
}

.main-header--issue {
  display: flex;
}

.main-header--issue:hover .main-header--buy::before,
.main-header--issue:focus .main-header--buy::before,
.main-header--issue:active .main-header--buy::before {
  background-image: url(../img/cart-active.svg);
}

.main-header--issue:hover .main-header--amount,
.main-header--issue:focus .main-header--amount,
.main-header--issue:active .main-header--amount {
  color: #d2f9f9;
  border-color: #d2f9f9;
  background-color: rgba(210, 249, 249, 0.2);
}

.main-header--amount {
  display: inline-block;
  margin: auto 0 auto 8px;
  padding: 3px 9px;
  order: 1;
  background-color: #344f45;
  color: #1f2d2e;
  border: 2px solid #20312b;
  border-radius: 50%;
}

.main-header--buy {
  font-size: 0;
}

.main-header--buy::before {
  content: '';
  display: inline-block;
  width: 40px;
  height: 38px;
  background: url(../img/cart.svg) center no-repeat;
  background-size: cover;
}

.shop-nav {
  background: url(../../img/nav-bg-new.jpg) center no-repeat;
  background-size: cover;
  font-size: 28px;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #d2f9f9;
}

.shop-nav--container {
  width: calc(66.66666666666667% - 30px);
  background-color: rgba(68, 102, 85, 0.49);
  border: 10px solid rgba(210, 249, 249, 0.24);
  text-align: center;
  padding: 30px 0;
}

.shop-nav--header {
  font-family: 'Cormorant Garamond', serif;
  font-size: 65px;
  letter-spacing: 10px;
  margin-bottom: 150px;
}

.shop-nav--list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.shop-nav--item {
  display: inline-block;
}

.shop-nav--link {
  color: #d2f9f9;
}

.shop-nav--link:hover,
.shop-nav--link:focus,
.shop-nav--link:active {
  color: #1b1d1d;
}

.shop {
  padding-bottom: 100px;
  padding-top: 35px;
  text-align: center;
}

.shop--alt-nav {
  position: fixed;
  top: 50%;
  right: 5%;
}

.shop--header {
  font-size: 48px;
  margin-bottom: 80px;
}

.shop--article {
  margin-top: 50px;
}

.shop--list {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: flex-start;
}

.shop--item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(25% - 30px);
  margin-bottom: 30px;
}

.product {
  display: flex;
  flex-direction: column;
}

.product--img {
  display: none;
  max-height: 200px;
  width: auto;
  transition: transform 0.4s ease-in-out;
}

.product--img:hover {
  transform: scale(1.5);
}

.product--img__active {
  display: inline-block;
}

.product--more-info {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.product--about {
  flex-grow: 1;
}

.product--color {
  font-size: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  border: 1px solid #1f2d2e;
  background-color: red;
  margin-bottom: 10px;
  transition: border 0.2s ease-in-out;
}

.product--color:hover,
.product--color:active {
  border: 2px solid #faee01;
}

.product--color__active {
  border: 2px solid #faee01;
}

.product--about {
  margin-bottom: 5px;
}

.product--size {
  margin-bottom: 10px;
}

.product--price {
  margin-bottom: 20px;
  display: block;
  font-size: 27px;
  color: #028e72;
  text-transform: uppercase;
  font-style: italic;
}

.product--buy {
  align-self: center;
}

.red {
  background-color: #822722;
}

.beige {
  background-color: #f6f3a5;
}

.blue {
  background-color: #002157;
}

.dark-blue {
  background-color: #2e3343;
}

.pink {
  background-color: #e38a8a;
}

.sky-blue {
  background-color: #6ebac5;
}

.gray {
  background-color: #b0b0b0;
}

.burgundy {
  background-color: #601419;
}

.brown {
  background-color: #754c24;
}

.flesh {
  background-color: #b6996a;
}

.black {
  background-color: #171d1d;
}

.dark-brown {
  background-color: #592211;
}

.green {
  background-color: #006954;
}

.white {
  background-color: #FAEEDC;
}

.white-brown {
  background-color: #B79F82;
}

.mustard {
  background-color: #FDA859;
}

.violet {
  background-color: #bda7b3;
}

.dark-violet {
  background-color: #523f45;
}

.bordo {
  background-color: #270c1d;
}

.alt-nav--item {
  text-align: right;
}

.alt-nav--link {
  margin-top: 5px;
  font-style: italic;
}

.alt-nav--link::after {
  content: "";
  display: inline-block;
  width: 12px;
  height: 12px;
  margin-left: 15px;
  border: 2px solid #1f2d2e;
  border-radius: 50%;
}

.alt-nav--link:hover .alt-nav--text {
  display: inline-block;
}

.alt-nav--link:hover::after {
  background-color: #1f2d2e;
}

.alt-nav--link__active .alt-nav--text {
  display: inline-block;
}

.alt-nav--link__active:after {
  background-color: #1f2d2e;
}

.alt-nav--text {
  display: none;
}

@media screen and (max-width: 992px) {
  .main-header--nav {
    width: auto;
    margin-left: auto;
    order: 1;
  }

  .main-header--list {
    display: block;
    position: absolute;
    top: 100%;
    right: 0;
    background-color: #446655;
  }

  .main-header--item__active .main-header--link {
    background-color: rgba(31, 45, 46, 0.4);
  }

  .main-header--item {
    display: block;
    font-size: 23px;
    text-align: center;
    border: 2px solid rgba(210, 249, 249, 0.7);
    border-bottom: none;
  }

  .main-header--item:last-child {
    border-bottom: 2px solid rgba(210, 249, 249, 0.7);
  }

  .main-header--link {
    padding: 20px 30px;
  }

  .main-header--link:hover,
  .main-header--link:focus {
    background-color: rgba(31, 45, 46, 0.4);
  }

  .main-header--link:active {
    background-color: rgba(31, 45, 46, 0.55);
  }

  .main-header--mobile {
    display: block;
  }

  .shop-nav--container {
    width: calc(83.33333333333334% - 30px);
  }

  .shop--header {
    margin-bottom: 35px;
  }

  .shop--list {
    justify-content: space-between;
  }

  .shop--item {
    width: calc(33.333333333333336% - 30px);
  }

  .alt-nav {
    display: none;
  }
}

@media screen and (max-width: 767px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }

  .main-header--amount {
    margin-left: 3px;
    padding: 1px 6px;
    font-size: 16px;
  }

  .main-header--buy::before {
    width: 33px;
    height: 31px;
  }

  .shop-nav--container {
    padding: 10px 0;
  }

  .shop-nav--header {
    margin-bottom: 15px;
    letter-spacing: 4px;
  }

  .shop-nav--item {
    flex-basis: 50%;
    margin-bottom: 12px;
  }

  .shop--header {
    margin-bottom: 0;
  }

  .shop--item {
    width: calc(50% - 30px);
  }

  .shop--item {
    margin-top: 30px;
  }

  .product--color {
    width: 22px;
    height: 22px;
  }
}

@media screen and (max-width: 576px) {
  .button {
    padding: 1px 10px;
  }

  .main-header {
    padding: 2px 0;
  }

  .main-header--buy::before {
    width: 37px;
    height: 35px;
  }

  .shop-nav--container {
    width: calc(100% - 30px);
  }

  .shop-nav--header {
    margin-bottom: 25px;
  }

  .shop-nav--list {
    display: block;
  }

  .shop-nav--item {
    display: block;
    margin-bottom: 7px;
  }
}

@media screen and (min-width: 0px) and (max-width: 340px) {
  .shop--item {
    width: 100%;
  }
}

@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .shop-nav {
    background-image: url("../../img/nav-bg-25.jpg");
  }
}